* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img { 
  height: 200px;
  width: 100%;
}

input {
  height: 35px;
  padding-bottom: 1em;
  padding-left: 0;
  outline: 0;
  border: none;
  border-bottom: 4px solid #e4e4e4;
  transition: border-color .4s;
}

input:focus {
  border-color: #c1c1c1;
}

button {
  height: 40px;
  background: none;
  outline: 0;
}